.pdf-text-container {
    width: 210mm; /* A4 width */
    min-height: 297mm; /* A4 height */
    margin: auto; /* Center the container */
    padding: 20mm; /* Reasonable padding */
    border: 1px solid #ddd; /* Optional: adds a border */
    background: #fff; /* White background */
    box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* Optional: adds a subtle shadow */
    overflow: auto; /* Adds scroll for overflow content */
    font-family: 'Times New Roman', serif; /* Typical word document font */
    font-size: 24px; /* Standard font size */
    line-height: 0.8; /* Standard line height */
    overflow-wrap: break-word;
    white-space: pre-wrap; 
  }


  .analysis-results {
    margin-top: 20px;
    overflow-y: auto;
    width: 210mm;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
    color: #333;
    font-family: Arial, sans-serif;
    font-size: 14px;
    line-height: 1.5;
    overflow-wrap: break-word;
    white-space: pre-wrap; /* Keeps the text formatting and line breaks */
  }